        font-weight: 300;
        letter-spacing: 0.2px;
    }
    
    .filter__item-btn {
        display: inline-flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        cursor: pointer;
        height: 30px;
        -webkit-transition: 0.4s ease;
        -moz-transition: 0.4s ease;
        transition: 0.4s ease;
    }
    
    .filter__item-btn input {
        display: block;
        position: relative;
        width: auto;
        background-color: transparent;
        border: none;
        font-size: 16px;
        color: #fff;
        cursor: pointer;
        height: 30px;
        margin-right: 12px;
        font-weight: 500;
    }
    
    .filter__item-btn span {
        position: relative;
        width: 16px;
        height: 16px;
        display: block;
        margin-top: 1px;
    }
    
    .filter__item-btn span:before,
    .filter__item-btn span:after {
        content: '';
        position: absolute;
        display: block;
        width: 16px;
        height: 2px;
        background-color: #fff;
        left: 0;
        -webkit-border-radius: 4px;
        border-radius: 4px;
        top: 50%;
        -webkit-transition: 0.4s ease;
        -moz-transition: 0.4s ease;
        transition: 0.4s ease;
        margin-top: -5px;
        opacity: 0.75;
    }
    
    .filter__item-btn span:after {
        margin-top: 2px;
        width: 10px;
    }
    
    .filter__item-btn[aria-expanded="true"] span:before,
    .filter__item-btn[aria-expanded="true"] span:after {
        opacity: 1;
    }
    
    .filter__item-btn[aria-expanded="true"] span:before {
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        transform: rotate(45deg);
        margin-top: -1px;
    }
    
    .filter__item-btn[aria-expanded="true"] span:after {
        margin-top: -1px;
        width: 16px;
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        transform: rotate(-45deg);
    }
    
    .filter__item-btn:hover span:before,
    .filter__item-btn:hover span:after {
        opacity: 1;
    }
    
